html {
  box-sizing: border-box;
  font-family: @fontStack;
  font-size: @baseTextSize;
  color: @baseTextColor;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background: #fff;
  margin: 0;
  padding: 0;
}
html, body {
  min-height: 100%;
}
h1 {

}
a {
  background: transparent; // IE10
}
article, aside, details,
figcaption, figure, footer,
header, hgroup, main,
nav, section, summary{
  display: block;
}
p, ul, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}
ul {
  padding: 0;
}
img {
  border: 0;
  width: 100%;
  vertical-align: middle;
}
li{
  list-style: none;
}
h1, h2, h3, h4, h5, h6{
  font: inherit;
}

.container-global {
  padding: 0 .5rem;
  display: flex;
  flex-direction: column;
  max-width: 768px;
  margin: auto;
  width: 100%;
}
.container {
  display: flex;
  margin: .2rem -.3rem;
  flex-wrap: wrap;
}


/*480 BREAKPOINT*/

@media (min-width: 480px) and (max-width: 767px){
  .container-global {
    padding: 0 2rem;
    display: flex;
    flex-direction: column;
  }
  .container {
    margin: .5rem -.5rem;
  }


}

/*768 BREAKPOINT*/

@media (min-width: 768px) {
  .container-global {
    padding: 0 3.4rem;
  }
  .container {
    margin: 0 -1rem;
  }
}