/*HEADER*/
.header {
  display: flex;
  padding-top: 2.7rem;
  font-size: 1.8rem;
  line-height: 2.4rem;
  margin-bottom: 1.3rem;
  &__heading {
    text-transform: uppercase;
    margin-right: 1.5rem;
    font-family: 'Playfair Display', serif;
    line-height: 3.5rem;
    font-weight: 300;
    font-size: 2.6rem;
  }
  &__underline {
    border-bottom: 2px solid @baseTextColor;
    flex: 1;
    margin-bottom: 7px;
  }
}
/*MAIN*/
.main {
  &__photo {
    position: relative;
    >img {
      width: 100%;
    }
  }
  &__date {
    position: absolute;
    width: 80px;
    height: 53px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.8rem;
    line-height: 2.4rem;
    transition: all 500ms 200ms;
    background-image: none;
    background-color: rgba(255, 255, 255, .8);
    top: 6px;
    left: 6px;
    padding: .7px;
    &-day {
      font-size: 1.8rem;
      margin-bottom: 0;
      line-height: 21px;
    }
    &-month {
      text-transform: lowercase;
      font-size: 1.4rem;
      line-height: 1.8rem;
    }
  }
  &__heading {
    font-size: 1.3rem;
    line-height: 1.8rem;
    padding: 1.2rem 0 .5rem;
    display: none;
    h3 {
      font-size: 1.3rem;
      line-height: 1.8rem;
      font-weight: 600;
      letter-spacing: .5px;
    }
    p {
      color: @secondaryTextColor;
      font-size: 1.3rem;
      line-height: 1.8rem;
    }
  }
  /*CONTENT*/
  .row {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    order: 2;
    flex-basis: 100%;
    &-75 {
      flex-basis: 100%;
      order: 1;
      flex-wrap: wrap;
    }
  }

  .col {
    width: 25%;
    padding: .3rem;
    transition: all 300ms 100ms;
      &.double {
        display: flex;
        align-items: flex-start;
        flex-basis: 25%;
        order: 1;
        >img {
          height: 212%;
          max-height: 220px;
          max-width: 160px;
        }
      }
    &.col--date {
      display: flex;
      justify-content: center;
      align-items: center;
      background: url("./../img/arrow-r.png") ~'calc(100% - 30px)' 40% no-repeat rgba(255, 255, 255, 1);
      background-position-x: ~'calc(100% - 5px)';
      flex-direction: column;
      cursor: pointer;
      >span {
        font-size: 3.6rem;
      }
      >span:first-child {
        line-height: 1.5rem;
        font-size: 1.8rem;
        margin-top: .6rem;
      }
      >span:last-child {
        font-size: 1.4rem;
        padding: .3rem 0;
      }
    }
    &:nth-child(5),
    &:nth-child(6),
    &:last-child {
      display: block;
    }
    &:nth-child(5).col--date {
      display: flex;
    }
  }
  .col.double {
    display: none;
  }
}

.paginator {
  display: flex;
  padding: 1.8rem 0 2.4rem 0;
  align-items: center;
  a {
    margin-left: 2.7rem;
    font-size: 1.3rem;
    color: #000;
    text-decoration: none;
    line-height: 32px;
    &:nth-child(3) {
      margin-left: 1.5rem;
    }
    &:last-child {
      margin-left: 3.8rem;
    }
    &:hover {
      text-decoration: underline;
    }
  }
  &__title {
    color: #848484;
    font-size: 1.3rem;
    line-height: 1.8rem;
    display: none;
  }
  &__next {

  }
  &__page {
   &.active {
     border-radius: 50%;
     background-color: #e6e6e6;
     font-weight: 700;
     padding: 0 1.3rem;
   }
    &:nth-child(2) {
      margin-left: 0;
    }
  }
}
.container > .row-md:nth-child(2) > .row:last-child {
  display: none;
}
/*BANNER*/
.banner-320 {
  display: flex;
  flex-direction: column;
  background: url('../img/banner-bg.png') -7px 34% no-repeat #fbf6e8;
  padding: 5px 7px 8px 37%;
  margin: .7rem .5rem 1rem .5rem;
  span {
    font-size: 10px;
    color: #bebebe;
  }
  h4 {
    font-size: 1.3rem;
    line-height: 2.2rem;
    letter-spacing: .5px;
    font-weight: 700;
 }
  p {
    font-size: 1.2rem;
    line-height: 1.6rem;
    letter-spacing: -.3px;
  }
}





/*480 BREAKPOINT*/

@media (min-width: 480px) and (max-width: 767px) {
  .banner-320 {
    display: none;
  }
  .header {
    padding-top: .9rem;
    margin-bottom: 3.1rem;
    &__heading {
      font-size: 4rem;
      line-height: 3.6rem;
    }
    &__underline {
      margin-bottom: 0;
    }
  }
  .main {
    .col.double {
      display: block;
      flex-basis: 40%;
      padding-left: 15px;
      align-self: stretch;
      height: 100%;
      margin-bottom: -28%;
      > img {
        max-height: 100%;
        height: initial;
        max-width: 100%;
      }
    }
    &__date {
      top: 1rem;
      left: 1rem;
      background: rgba(255, 255, 255, .8);
      padding: .7px;
      &-day {
        font-size: 1.8rem;
        margin-bottom: 0;
        line-height: 21px;
      }
      &-month {
        font-size: 1.4rem;
        line-height: 1.8rem;
        letter-spacing: 0.4px;
      }
    }
  }
  .main .col {
    width: 33.3%;
    padding: .5rem;
      background-position-x: ~'calc(100% - 16px)';
      >span:first-child {
        font-size: 1.8rem;
        margin-top: .6rem;
      }
      >span:last-child {
        font-size: 1.4rem;
        padding: .3rem 0;
      }
    }
  .row-md {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    &:first-child .row-75:first-child .col {
  /*    max-height: 63px;*/
    }
    &:first-child .row-75:last-child .col {
      &:nth-child(7),
      &:nth-child(8) {
        display: none;
      }
    }
    .row:last-of-type {
      order: 1;
      flex-basis: 60%;
      .col {
        width: 33.3%;
      }
    }
    &:last-of-type {
      .row {
        &:last-of-type {
          flex-basis: 100%;
          .col {
            width: 20%;
          }
        }
      }
      .double {
        display: none;
      }
    }
  }
  .paginator {
    padding-left: 0;
    &__title {
      display: inline;
    }
    &__page:nth-child(2) {
      margin-left: 2.5rem;
    }
  }
  .row-md .row,
  .row-md .row-75 {
    flex-basis: 60%;
  }

  .container > .row-md:nth-child(2) > .row:first-child {
    flex-basis: 100%;
    >.col {
      width: 20%;
    }
  }
  .container > .row-md:nth-child(2) > .row:last-child {
    display: flex;
  }
  .paginator {
    padding: 2.2rem 0 2.4rem 0;
    a {
      margin-left: 3.8rem;
      &:nth-child(3) {
        margin-left: 2.4rem;
      }
    }
  }
}

/*768 BREAKPOINT*/

@media (min-width: 768px) {
  .banner-320 {
    display: none;
  }
  .header {
    padding-bottom: 1.5rem;
    margin-bottom: 1.6rem;
    &__heading {
      font-size: 4rem;
    }
    &__underline {
      margin-bottom: 0;
    }
  }
  .row-md .row {
    &:nth-of-type(6) {
      order: 4;
    }
    .col {
      width: 25%;
      padding: 1rem;

    }
  }
  .col.double {
    padding: 10px;
    &:nth-of-type(5) {
      order: 3;
    }
  }
  .main {
    .col.double {
      display: block;
    }
    &__date {
    top: 1.9rem;
    left: 2.1rem;
    width: initial;
    height: initial;
    background: url("./../img/arrow-r.png") ~'calc(100% - 18px)' 35px no-repeat rgba(255, 255, 255, 0.8);
    padding: 2.6rem 3.8rem 2.3rem;
      &-day {
        font-size: 3.6rem;
        line-height: 2.4rem;
        margin-bottom: .6rem;
      }
      &-month {
        font-size: 1.8rem;
        line-height: 2.4rem;
      }
    }
    &__heading {
      display: block;
    }
  }
  .row-md {
    display: flex;
    flex-wrap: wrap;
    .row {
      flex-basis: 100%;
      &.row-75 {
        flex-basis: 75%;
      }

    }
    &:first-child {
      .row.row-75 .col {
        width: 33.3%;
      }
      .row.row-75:nth-of-type(4) {
        order: 3;
        flex-basis: 100%;
        .col {
          width: 25%;
        }
      }
    }
    &:nth-child(2) {
      .row:first-child {
        order: 1;
        flex-basis: 75%;
        .col {
          width: 33.3%;
        }
      }
      .col.double {
        order: 1;
        flex-basis: 25%;
      }
    }
  }
  .main .col.col--date {
    background-position: ~'calc(100% - 29px)' 48px;
    >span:first-child {
      margin-top: 14px;
      font-size: 3.6rem;
      margin-bottom: 7px;
    }
    >span:last-child {
      font-size: 1.8rem;
      padding: .3rem 0;
    }
  }
  .paginator {
    padding-left: 0;
    padding-top: 2.5rem;
    &__title {
      display: inline;
    }
    &__page:nth-child(2) {
      margin-left: 2.5rem;
    }
  }
  .container > .row-md:nth-child(2) > .row:last-child {
    display: flex;
  }
}